import { TOURNAMENT_REPORT } from "../assets/tournament_report";

export const generateLeaderboard = (decksPerformance: any): any[] => {
  if (Object.keys(decksPerformance).length === 0) return [];
  let ret: any = Object.keys(decksPerformance).map((deck: any) => ({
    "deck": deck,
    "1st Place": decksPerformance[deck]["original"]["1st Place"],
    "2nd Place": decksPerformance[deck]["original"]["2nd Place"],
    "Top 4": decksPerformance[deck]["original"]["Top 4"],
    "Top 8": decksPerformance[deck]["original"]["Top 8"],
    "Top 16": decksPerformance[deck]["original"]["Top 16"],
    "Participation Score": decksPerformance[deck]["weighted"]["1st Place"] * 10
      + decksPerformance[deck]["weighted"]["2nd Place"] * 8
      + decksPerformance[deck]["weighted"]["Top 4"] * 6
      + decksPerformance[deck]["weighted"]["Top 8"] * 4
      + decksPerformance[deck]["weighted"]["Top 16"] * 2,
  }));
  ret.sort((a: any, b: any) => {
    if (a["1st Place"] > b["1st Place"]) return -1;
    if (a["1st Place"] < b["1st Place"]) return 1;
    if (a["2nd Place"] > b["2nd Place"]) return -1;
    if (a["2nd Place"] < b["2nd Place"]) return 1;
    if (a["Top 4"] > b["Top 4"]) return -1;
    if (a["Top 4"] < b["Top 4"]) return 1;
    if (a["Top 8"] > b["Top 8"]) return -1;
    if (a["Top 8"] < b["Top 8"]) return 1;
    if (a["Top 16"] > b["Top 16"]) return -1;
    if (a["Top 16"] < b["Top 16"]) return 1;
    if (a["Participation Score"] > b["Participation Score"]) return -1;
    if (a["Participation Score"] < b["Participation Score"]) return 1;
    if (a["deck"] < b["deck"]) return -1;
    if (a["deck"] > b["deck"]) return 1;
    return 0;
  });
  let rank = 1;
  ret[0]["Placement Ranking"] = rank;
  for (let i = 1; i < ret.length; i++) {
    if (ret[i]["Participation Score"] !== ret[i - 1]["Participation Score"]) { 
      rank = i + 1;
    }
    ret[i]["Placement Ranking"] = rank;
  }
  return ret;
}

export const topNPlacementRanking = (leaderboard: any, N: number): any[] => {
  const t = JSON.parse(JSON.stringify(leaderboard));
  t.sort((a: any, b: any) => {
    if (a["Placement Ranking"] < b["Placement Ranking"]) return -1;
    if (a["Placement Ranking"] > b["Placement Ranking"]) return 1;
    if (a["Participation Score"] > b["Participation Score"]) return -1;
    if (a["Participation Score"] < b["Participation Score"]) return 1;
    if (a["deck"] < b["deck"]) return -1;
    if (a["deck"] > b["deck"]) return 1;
    return 0;
  });
  t.length = Math.min(N, t.length);
  return t;
}

export const topNParticipationScore = (leaderboard: any, N: number): any[] => {
  const t = JSON.parse(JSON.stringify(leaderboard));
  t.sort((a: any, b: any) => {
    if (a["Participation Score"] > b["Participation Score"]) return -1;
    if (a["Participation Score"] < b["Participation Score"]) return 1;
    if (a["Placement Ranking"] < b["Placement Ranking"]) return -1;
    if (a["Placement Ranking"] > b["Placement Ranking"]) return 1;
    if (a["deck"] < b["deck"]) return -1;
    if (a["deck"] > b["deck"]) return 1;
    return 0;
  });
  t.length = Math.min(N, t.length);
  return t;
}

export const includesMultiple = (s: string, l: string[]): boolean => {
  for (let i = 0; i < l.length; i++) {
    if (s.includes(l[i])) {
      return true;
    }
  }
  return false;
}

export const getData = (tournament: any = [], startDate: string, endDate: string): any => {
  var rName: string = "", 
      rDate: string = "", 
      rPlayers: string = "0", 
      rSource: string = "", 
      rResults: any = {},
      decksPerformance: any = {},
      tournamentRecord: any = {},
      maxPlacement: any = {"1st Place": 0, "2nd Place": 0, "Top 4": 0, "Top 8": 0, "Top 16": 0};

  const initializeVariable = () => {
    rName = "";
    rDate = "";
    rPlayers = "0";
    rSource = "";
    rResults = {};
  }

  const isValidVariable = () => {
    return rName !== "" 
      && rDate !== "" 
      && rPlayers !== "0" 
      && rSource !== "" 
      && Object.keys(rResults).length > 0;
  }

  const updateData = () => {
    if (!isValidVariable()) {
      return;
    }
    for (const pos in rResults) {
      const decks = rResults[pos];
      for (let i = 0; i < decks.length; i++) {
        const deck = decks[i];
        if (!(deck in decksPerformance)) {
          decksPerformance[deck] = {}
          decksPerformance[deck]["original"] = {"1st Place": 0, "2nd Place": 0, "Top 4": 0, "Top 8": 0, "Top 16": 0};
          decksPerformance[deck]["weighted"] = {"1st Place": 0, "2nd Place": 0, "Top 4": 0, "Top 8": 0, "Top 16": 0};
          tournamentRecord[deck] = [];
        }
        decksPerformance[deck]["original"][pos] += 1;
        decksPerformance[deck]["weighted"][pos] += Math.max(1, Math.floor(parseInt(rPlayers) / 100));
        tournamentRecord[deck].push({
          "name": rName,
          "date": rDate,
          "player": rPlayers,
          "source": rSource,
          "result": pos,
        });
      }
    }
  }

  for (let _tournament in TOURNAMENT_REPORT) {
    let report = TOURNAMENT_REPORT[_tournament];
    initializeVariable();
    for (let i = 0; i < report.length; i++) {
      let s = report[i].trim();
      if (s.includes("Tournament:")) {
        rName = s.split(": ")[1];
      } else if (s.includes("Date:")) {
        rDate = s.split(": ")[1];
      } else if (s.includes("Players:")) {
        rPlayers = s.split(": ")[1];
      } else if (s.includes("Source:")) {
        rSource = s.split(": ")[1];
      } else if (s.includes("Results:")) {
        rResults = {}
      } else if (includesMultiple(s, ["1st Place:", "2nd Place:", "Top 4:", "Top 8:", "Top 16:"])) {
        rResults[s.split(": ")[0]] = s.split(": ")[1].split(",").map((item: any) => item.trim());
      } else {
        if (startDate <= rDate && rDate <= endDate) {
          if (tournament.length === 0 || includesMultiple(rName, tournament)) {
            updateData();
            initializeVariable();
          }
        }
      }
    }
  }

  for (let deck in decksPerformance) {
    for (let placement in decksPerformance[deck]["original"]) {
      maxPlacement[placement] = Math.max(maxPlacement[placement], decksPerformance[deck]["original"][placement]);
    }
  }
  return [decksPerformance, tournamentRecord, maxPlacement];
}

// https://stackoverflow.com/a/27709336
export const getGradientColor = (startColor: string, endColor: string, percent: number) => {
  startColor = startColor.replace(/^\s*#|\s*$/g, '');
  endColor = endColor.replace(/^\s*#|\s*$/g, '');
  if (startColor.length === 3) {
    startColor = startColor.replace(/(.)/g, '$1$1');
  }
  if (endColor.length === 3) {
    endColor = endColor.replace(/(.)/g, '$1$1');
  }
  var startRed = parseInt(startColor.substr(0, 2), 16),
      startGreen = parseInt(startColor.substr(2, 2), 16),
      startBlue = parseInt(startColor.substr(4, 2), 16);
  var endRed = parseInt(endColor.substr(0, 2), 16),
      endGreen = parseInt(endColor.substr(2, 2), 16),
      endBlue = parseInt(endColor.substr(4, 2), 16);
  var diffRed: any = endRed - startRed;
  var diffGreen: any = endGreen - startGreen;
  var diffBlue: any = endBlue - startBlue;
  diffRed = ((diffRed * percent) + startRed).toString(16).split('.')[0];
  diffGreen = ((diffGreen * percent) + startGreen).toString(16).split('.')[0];
  diffBlue = ((diffBlue * percent) + startBlue).toString(16).split('.')[0];
  if (diffRed.length === 1) diffRed = '0' + diffRed
  if (diffGreen.length === 1) diffGreen = '0' + diffGreen
  if (diffBlue.length === 1) diffBlue = '0' + diffBlue
  return '#' + diffRed + diffGreen + diffBlue;
};

