import React from "react";
import { DatePicker, Form, Button, Select } from "antd";
import moment from "moment";
import { tournamentList } from "./Util";

interface LeaderboardSearchProps {
  setDate: (e: any) => void,
  setTournament: (e: any) => void,
};

const { Option } = Select;
const { RangePicker } = DatePicker;

const LeaderboardSearch = ({
  setDate,
  setTournament,
}: LeaderboardSearchProps) => {
  const [form] = Form.useForm();

  return (
    <Form 
      form={form} 
      initialValues={{ "date": [moment().add(-7, "days"), moment()], "tournament": [] }} 
      layout="inline" 
      style={{ marginBottom: "20px" }}
      onFinish={() => {
          setDate(form.getFieldValue("date"));
          setTournament(form.getFieldValue("tournament"));
        }
      }
    >
      <Form.Item name="tournament">
        <Select
          mode="multiple"
          placeholder="Default tournament: all"
          style={{ width: "250px" }}
        >
          {
            tournamentList.map((v: any) => <Option key={v} value={v}>{v}</Option>)
          }
        </Select>
      </Form.Item>
      <Form.Item name="date" rules={[{ required: true }]}>
        <RangePicker
          disabledDate={(cur: any) => cur > moment()}
          style={{ borderRadius: "6px" }}
        />
      </Form.Item>
      <Button type="primary" htmlType="submit" style={{ borderRadius: "6px" }} >
        Search
      </Button>
    </Form>
  );
}

export default LeaderboardSearch;