import React from "react";
import { Modal, Table, Row, Col, Image } from "antd";
import { ColumnType } from "antd/lib/table";
import { deckDetail, includesMultiple } from "./Util";

interface DeckParticipationProps {
  deck: string,
  setDeck: (e: any) => void,
  record: any,
}

const DeckParticipation = ({
  deck,
  setDeck,
  record,
}: DeckParticipationProps) => {

  const columns: ColumnType<any>[] = [
    {
      dataIndex: "result",
      key: "result",
      title: "Result",
      sorter: {
        compare: (a: any, b: any) => {
          if (a.result < b.result) return -1;
          if (a.result > b.result) return 1;
          return 0;
        },
      },
      showSorterTooltip: false,
    },
    {
      dataIndex: "name",
      key: "name",
      title: "Tournament Name",
    },
    {
      dataIndex: "date",
      key: "date",
      title: "Date",
      sorter: {
        compare: (a: any, b: any) => {
          if (a.date < b.date) return -1;
          if (a.date > b.date) return 1;
          return 0;
        },
      },
      showSorterTooltip: false,
      defaultSortOrder: "descend",
    },
    {
      dataIndex: "source",
      key: "source",
      title: "Source",
      render: (value: any) => <a target="_blank" rel="noopener noreferrer" href={value}>Link</a>,
    },
  ]

  return (
    <Modal
      title={`${deck} Participation`}
      visible={deck !== ""}
      onCancel={() => setDeck("")}
      cancelButtonProps={{ style: { borderRadius: "6px" } }}
      okButtonProps={{ hidden: true }}
      width={1000}
    >
      <Row gutter={[16, 16]}>
        <Col xs={24} md={8} style={{ textAlign: "center" }}>
          {
            includesMultiple(deck, Object.keys(deckDetail)) ?
            <Image
              width={200}
              src={deckDetail[deck]["image"] + "&il&w=360&h=523"}
            /> :
            <Image
              width={200}
              height={200}
              src="error"
              fallback="data:image/png;base64,"
            />
          }
        </Col>
        <Col xs={24} md={16}>
          <Table
            columns={columns}
            dataSource={record[deck]}
            size="small"
            pagination={{ pageSize: 5 }}
            scroll={{ x: "max-content" }}
          />
        </Col>
      </Row>
    </Modal>
  )
}

export default DeckParticipation;