import React, { useState, useEffect } from "react";
import { Table, Button, Row, Col, Card, Tooltip } from "antd";
import { ColumnType } from "antd/lib/table";
import { generateLeaderboard, getData, getGradientColor, deckDetail, includesMultiple, topNPlacementRanking, topNParticipationScore } from "./Util";
import "./Leaderboard.css";
import DeckParticipation from "./DeckParticipation";
import LeaderboardSearch from "./LeaderboardSearch";
import moment from "moment";
import Avatar from "antd/lib/avatar/avatar";
import { QuestionCircleOutlined } from "@ant-design/icons";
import { LAST_UPDATED_AT } from "../assets/tournament_report";

const Leaderboard: React.FC<{}> = () => {
  
  const [clickedDeck, setClickedDeck] = useState<string>("");
  const [date, setDate] = useState<any>([moment().add(-7, "days"), moment()]);
  const [tournament, setTournament] = useState<any>([]);
  const data = getData(tournament, date[0].format("YYYY-MM-DD"), date[1].format("YYYY-MM-DD"));
  const [decksPerformance, setDecksPerformance] = useState<any>(data[0]);
  const [tournamentRecord, setTournamentRecord] = useState<any>(data[1]);
  const [maxPlacement, setMaxPlacement] = useState<any>(data[2]);
  const leaderboard = generateLeaderboard(decksPerformance);

  useEffect(() => {
    setDecksPerformance(data[0]);
    setTournamentRecord(data[1]);
    setMaxPlacement(data[2]);
    // eslint-disable-next-line
  }, [date, tournament]);

  const columns: ColumnType<any>[] = [
    {
      className: "deck",
      dataIndex: "deck",
      key: "deck",
      title: "Deck",
      sorter: {
        compare: (a, b) => {
          if (a.deck < b.deck) return -1;
          if (a.deck > b.deck) return 1;
          return 0;
        },
      },
      render: (value: any) => {
        return (
          <span>
            {(
              includesMultiple(value, Object.keys(deckDetail)) ? 
                <Avatar src={deckDetail[value]["image"] + "&crop=70,70,15,30&w=100"} /> :
                <Avatar />
            )}
            <Button
              type="link" 
              onClick={() => setClickedDeck(value)} 
              style={{fontSize: 16, fontFamily: 'Lato'}}
            >
              {value}
            </Button>
          </span>
        )
      },
      showSorterTooltip: false,
      fixed: "left",
    },
    {
      dataIndex: "1st Place",
      key: "1st Place",
      title: "1st Place",
      sorter: {
        compare: (a, b) => a["1st Place"] - b["1st Place"],
      },
      render: (value: any) => {
        return {
          props: {
            style: { background:  getGradientColor("#e8f5e9", "#81c784", value / maxPlacement["1st Place"]) }
          },
          children: <div>{value}</div>
        };
      },
      showSorterTooltip: false,
    },
    {
      dataIndex: "2nd Place",
      key: "2nd Place",
      title: "2nd Place",
      sorter: {
        compare: (a, b) => a["2nd Place"] - b["2nd Place"],
      },
      render: (value: any) => {
        return {
          props: {
            style: { background:  getGradientColor("#e8f5e9", "#81c784", value / maxPlacement["2nd Place"]) }
          },
          children: <div>{value}</div>
        };
      },
      showSorterTooltip: false,
    },
    {
      dataIndex: "Top 4",
      key: "Top 4",
      title: "Top 4",
      sorter: {
        compare: (a, b) => a["Top 4"] - b["Top 4"],
      },
      render: (value: any) => {
        return {
          props: {
            style: { background:  getGradientColor("#e8f5e9", "#81c784", value / maxPlacement["Top 4"]) }
          },
          children: <div>{value}</div>
        };
      },
      showSorterTooltip: false,
    },
    {
      dataIndex: "Top 8",
      key: "Top 8",
      title: "Top 8",
      sorter: {
        compare: (a, b) => a["Top 8"] - b["Top 8"],
      },
      render: (value: any) => {
        return {
          props: {
            style: { background:  getGradientColor("#e8f5e9", "#81c784", value / maxPlacement["Top 8"]) }
          },
          children: <div>{value}</div>
        };
      },
      showSorterTooltip: false,
    },
    {
      dataIndex: "Top 16",
      key: "Top 16",
      title: "Top 16",
      sorter: {
        compare: (a, b) => a["Top 16"] - b["Top 16"],
      },
      render: (value: any) => {
        return {
          props: {
            style: { background:  getGradientColor("#e8f5e9", "#81c784", value / maxPlacement["Top 16"]) }
          },
          children: <div>{value}</div>
        };
      },
      showSorterTooltip: false,
    },
    {
      dataIndex: "Placement Ranking",
      key: "Placement Ranking",
      title: "Placement Ranking",
      defaultSortOrder: "ascend",
      sorter: {
        compare: (a, b) => a["Placement Ranking"] - b["Placement Ranking"],
      },
      showSorterTooltip: false,
    },
    {
      dataIndex: "Participation Score",
      key: "Participation Score",
      title: "Participation Score",
      sorter: {
        compare: (a, b) => a["Participation Score"] - b["Participation Score"],
      },
      showSorterTooltip: false,
    },
  ];
  
  return (
    <div>
      <div style={{ textAlign: "center", marginBottom: "50px" }}>
        <h1>Duel Links Tournament Leaderboard</h1>
      </div>
      <Row>
        <Col style={{ marginLeft: "auto" }}>
          <LeaderboardSearch
            setDate={setDate}
            setTournament={setTournament}
          />
        </Col>
      </Row>
      <div style={{ marginBottom: "5px" }}>
        Last Updated: {LAST_UPDATED_AT}
      </div>
      <Row gutter={[16, 16]} style={{ marginBottom: "20px" }}>
        <Col xs={24} sm={12}>
          <Card style={{ borderRadius: "6px", textAlign: "center" }}>
            <Row align="middle" justify="center" style={{ marginBottom: "20px" }}>
              <span style={{ fontSize: "16px" }}>Top 5 Best Placement Ranking</span>
              &nbsp;&nbsp;
              <Tooltip 
                title="Placement Ranking of a deck is a ranking order
                      based first on the number of 1st place won, then 2nd,
                      and top 4, etc."
                style={{ borderRadius: "6px" }}
              >
                <QuestionCircleOutlined 
                  style={{ display: "inline-block", verticalAlign: "middle" }}
                />
              </Tooltip>
            </Row>
            <Row>
              <table style={{ width: "100%", fontSize: 16 }}>
                {
                  topNPlacementRanking(leaderboard, 5).map(
                    (v: any) =>
                      <tr>
                        <td style={{ textAlign: "left", height: "40px", borderBottom: "2px solid #fafafa" }}>
                         {
                           includesMultiple(v["deck"], Object.keys(deckDetail)) ?
                           <Avatar src={deckDetail[v["deck"]]["image"] + "&crop=70,70,15,30&w=100"} /> :
                           <Avatar />
                          }
                          &emsp;
                          {v["deck"]}
                        </td>
                        <td style={{ borderBottom: "2px solid #fafafa" }}>
                          {v["Placement Ranking"]}
                        </td>
                      </tr>
                  )
                }
              </table>
            </Row>
          </Card>
        </Col>
        <Col xs={24} sm={12}>
          <Card style={{ borderRadius: "6px", textAlign: "center" }}>
            <Row align="middle" justify="center" style={{ marginBottom: "20px" }}>
              <span style={{ fontSize: "16px" }}>Top 5 Best Participation Score</span>
              &nbsp;&nbsp;
              <Tooltip 
                title="Participation score of a deck is a ranking order
                      based on the total weighted point of their wins. 
                      1st place rewarded 10 pts, 2nd place rewarded 8 pts, etc.
                      The number of participants in that tournament will also affect
                      the weighted point."
                style={{ borderRadius: "6px" }}
              >
                <QuestionCircleOutlined 
                  style={{ display: "inline-block", verticalAlign: "middle" }}
                />
              </Tooltip>
            </Row>
            <Row>
              <table style={{ width: "100%", fontSize: 16 }}>
                {
                  topNParticipationScore(leaderboard, 5).map(
                    (v: any) =>
                      <tr>
                        <td style={{ textAlign: "left", height: "40px", borderBottom: "2px solid #fafafa" }}>
                         {
                           includesMultiple(v["deck"], Object.keys(deckDetail)) ?
                           <Avatar src={deckDetail[v["deck"]]["image"] + "&crop=70,70,15,30&w=100"} /> :
                           <Avatar />
                          }
                          &emsp;
                          {v["deck"]}
                        </td>
                        <td style={{ borderBottom: "2px solid #fafafa" }}>
                          {v["Participation Score"]}
                        </td>
                      </tr>
                  )
                }
              </table>
            </Row>
          </Card>
        </Col>
      </Row>
      <Table
        columns={columns}
        dataSource={leaderboard}
        rowKey={"deck"}
        pagination={{ pageSize: 100, hideOnSinglePage: true }}
        scroll={{ x: "max-content" }}
      />
      <DeckParticipation 
        deck={clickedDeck}
        setDeck={setClickedDeck}
        record={tournamentRecord}
      />
    </div>
  );
}

export default Leaderboard;