import React, { useEffect } from "react";
import ReactGA from "react-ga";
import "./App.css";
import "antd/dist/antd.css";
import Leaderboard from "./components/Leaderboard";
import { Row, Col } from "antd";

function App() {
  useEffect(() => {
    ReactGA.initialize("UA-180656918-1");
    ReactGA.pageview(window.location.pathname);
  });

  return (
    <Row justify="center" align="middle" style={{ minHeight: "100vh", margin: "150px 0", backgroundColor: "#f2f2f2" }}>
      <Col span={20}>
        <Leaderboard /> 
      </Col>
    </Row>
  );
}

export default App;
